.route-finder-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: auto;
}
h1{
  padding: 20px;
}
p{
  font-size: xx-large;
}

.planet-container {
    display: flex; /* Make the container a flex container */
    flex-wrap: wrap; /* Allow the items to wrap to the next line */
    justify-content: center; /* Center the items on the main axis (horizontally) */
    align-items: center; /* Center the items on the cross axis (vertically) */
    margin: 20px;
  }
  .search-button {
    width: 10%;
    margin-left: 1%;
    margin-right: 1%;
    height: 5%;
    background-color: rgb(100, 145, 49);
    border-color: black;
    border-width: 2px;
    border-radius: 10px;
    font-size: 24px;
    text-align: center;
    cursor: pointer;
  }
  
  .planet-selected {
    background-color: green;
    cursor: not-allowed;
  }
  
  .planet-from {
    background-color: blue;
  }
  
  .planet-to {
    color: red;
  }
  